.footer{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color: black;
    height: 70px;
    width: 100%;
    filter: brightness(0.4);
    transition: all 0.3s linear;

  }
  .footer:hover{
      filter: brightness(1);
  }

  .footerImage{
      height: 30px;
      /* filter: brightness(0.6); */
      transition: all 0.3s linear;
      margin-top: 6px;
      padding: 10px;
      border-radius: 10px;
      border-bottom: solid 3px black;
  }
  .footerImage:hover{
      filter: brightness(1);
      border-bottom: solid 3px white;
      border-radius: 10px;
      box-shadow: 0 0 15px white;
  }
